		whatIsCurrentFilter() {
			if (this.filterBy.value.value) {
				return this.filterBy.value.value
			}
			if (this.filterBy.value) {
				return this.filterBy.value
			}
			if (this.filterBy.targetValue) {
				return this.filterBy.targetValue
			}
			return 'تراکنش ها'
		}
  },

  watch: {},
  async created() {
    try {
      this.loading = true;
      const { data: UserDebtDetail } = await this.$http.get(
        "/manager/user-debt/detail",
        {
          params: {
            user_id: this.$route.query.id,
          },
        headers: {
          'Authorization': 'Bearer ' + localStorage.getItem('token'),
        },
        }
);
      const { data: UserDebtUserUnitDebt } = await this.$http.get(
        "/manager/user-debt/user-unit-debt",
        {
          params: {
            user_id: this.$route.query.id,
          },
        headers: {
          'Authorization': 'Bearer ' + localStorage.getItem('token'),
        },
        }
      );
      const { data: UserDebtAccountDetail } = await this.$http.get(
        "/manager/user-debt/account-detail",
        {
          params: {
            user_id: this.$route.query.id,
          },
        headers: {
          'Authorization': 'Bearer ' + localStorage.getItem('token'),
        },
        }
      );
      this.UserDebtUserUnitDebt = UserDebtUserUnitDebt;
      this.UserDebtAccountDetail = UserDebtAccountDetail;

      this.pageData = UserDebtDetail;
      // console.log(UserDebtDetail);
      this.loading = false;
    } catch (err) {
      this.loading = false;
      console.log(err);
    }
  },
  methods: {
    generateBgForBox,
    getUniqueString() {
      return uuidv4();
    },
  }
};
</script>

